import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./unit-of-measurement-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class UnitOfMeasurementAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
//	@ViewChild(ToastComponent) toast: ToastComponent;
 
unitOfMeasurementForm: FormGroup;
uomId = 0;  
ajaxData : UnitOfMeasurementData[];
unitofmeasurement= new UnitOfMeasurementData();	
config = new configDetails(); 
	
public uoms = [];
status;

msg = [];
bootstrapFromValues = [];

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.uomId).subscribe(params=>{
			if(params.uomId != undefined){
				this.uomId=params.uomId; 
			}
		}); 
	console.log("uomId=="+this.uomId);
	if(this.uomId == 0){
		this.status = "Add";
	}else{
		this.status = "Update";
		this.populateData();
		this.populateDropDownData();
	}
	}
	
    ngOnInit() {
		this.unitOfMeasurementForm = new FormGroup({			
			'uomName' : new FormControl('', [ Validators.required]),
			'uomShortname' : new FormControl('', [ Validators.required]),
			'uomParentId' : new FormControl(''),
			'uomRatio' : new FormControl(''),
			'uomEntryId' : new FormControl(''),
			'uomEntryDate' : new FormControl(''),
			'uomModifiedId' : new FormControl(''),
			'uomModifiedDate' : new FormControl(''),
		});
	}
	  
	   	get uomName(){ return this.unitOfMeasurementForm.get('uomName')};
		get uomShortname(){ return this.unitOfMeasurementForm.get('uomShortname')};
		get uomParentId(){ return this.unitOfMeasurementForm.get('uomParentId')};
		get uomRatio(){ return this.unitOfMeasurementForm.get('uomRatio')};
	    get uomEntryId(){ return this.unitOfMeasurementForm.get('uomEntryId')};
	    get uomEntryDate(){ return this.unitOfMeasurementForm.get('uomEntryDate')};
	    get uomModifiedId(){ return this.unitOfMeasurementForm.get('uomModifiedId')};
	    get uomModifiedDate(){ return this.unitOfMeasurementForm.get('uomModifiedDate')};
	 
	populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			    this.http.post<UnitOfMeasurementData>(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/uom-data ',
							 			JSON.stringify({ 'uomId' :this.uomId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.unitofmeasurement = data['populateData'][0];
																		console.log("uomModifiedId=="+this.unitofmeasurement.uomModifiedId);
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }
	 
	  populateDropDownData(){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
			  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/uom-populate/branch',
							 			JSON.stringify({ emp_id :'0'}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.uoms = data['populateBranch']
			  															});
			 	  
	 }
 
	   ngAfterViewInit(){ }	
	
 
		 toggleAnimation(){
			this._router.navigate(['inventory/unit-of-measurement-list']);      //path should be changed
		 }

 
	  	onSubmit() {
			this.bootstrapFromValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.unitofmeasurement[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
			if(!this.unitOfMeasurementForm.invalid){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});
			if(this.uomId == 0){ 
				this.unitofmeasurement['requestType'] = 'add';
			}else{
				this.unitofmeasurement['requestType'] = 'update';
				this.unitofmeasurement['uomId'] = this.uomId+'';
			}
			 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/uom',
									JSON.stringify(this.unitofmeasurement),
						{ headers, withCredentials: true }).subscribe(data => {
															if(data['successmsg'] == undefined){
																this.msg[0]='Error';
																this.msg[1]=data['errormsg'];
															}else{
																if(data['successmsg'].includes('Successfully')){
																	this.msg[0]='success';
																	this.msg[1]=data['successmsg'];
																}
															}
	//			  											this.toast.showtoast(this.msg);
															if(this.msg[0]!='Error'){
																this.unitOfMeasurementForm.reset();
																this.toggleAnimation();
															}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
//			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.unitofmeasurement);
	  }
 
  onDelete(){
		 this.unitofmeasurement['requestType'] = 'delete';
		this.unitofmeasurement['uomId'] = this.uomId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/uom',
									JSON.stringify(this.unitofmeasurement),
						{ headers, withCredentials: true }).subscribe(data => {			 											
			 											if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['successmsg'];
														}
//			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.unitOfMeasurementForm.reset();
//			 											this.list.getData();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
//			 												this.toast.showtoast(this.msg);
		 												});
	 }
	  	
	
}

export class UnitOfMeasurementData {

    uomId: string;
    uomName: string;
	uomShortname:string;
	uomParentId: string;
	uomRatio: string;
	uomEntryId: string;
	uomEntryDate: string;
	uomModifiedId:string;
	uomModifiedDate:string;
  
}
export class configDetails {
	configCustomerSob:string;
	configCustomerSoe:string;
	configCustomerAddress:string;
}


